h1 {
  font-size: 24px;
  text-transform: uppercase;
  color: $colorPrimary;
  max-width: 657px;
  margin-bottom: 30px;
  margin-top: 30px;
  &.m-black{
    color: $colorBlack;
  }
}

h2 {
  font-size: 24px;
  text-transform: uppercase;
  max-width: 657px;
  &.m-red{
    color: $colorPrimary;
  }

}

h3 {
  font-size: 18px;
  text-transform: uppercase;
}

p {
  line-height: normal;
  margin-block-end: 2em;

}
.text{
  line-height: normal;
  font-size: 16px;
  &__ul{
    margin: 40px 0 40px 0;
    padding-left:15px;
    list-style-type: disc;
  }
  &__li{
    margin-bottom: 20px;
    list-style-type: disc;
}

}